import { actiontypes } from "./Actiontypes";

export const data = {
  datas: [
    {
      states: "gujarat",
      cities: [{ names: "g1" }, { names: "g2" }, { names: "g3" }],
    },

    {
      states: "mharashtra",
      cities: [{ names: "m1" }, { names: "m2" }, { names: "m3" }],
    },

    {
      states: "Andhra",
      cities: [{ names: "a1" }, { names: "a2" }, { names: "a3" }],
    },
  ],
};

export const ReducerState_Data = (state = data, action) => {
  switch (action.type) {
    case actiontypes.STATECHECKED_DATA:
      var { name, checked, value } = action.event.target;
      const statechecked = state.datas.map((val) =>
        val.states === name
          ? { ...val, ischecked: true }
          : { ...val, ischecked: false }
      );
      const cityarr = statechecked.find((val) => val.ischecked === true); //object
      var citychecked = cityarr.cities.map((val) => {
        return {
          ...val,
          ischecked: true,
        };
      });
      return {
        datas: statechecked,
        city: citychecked,
      };

      break;

    case actiontypes.CITY:
      var name = action.event;
      var citychecked = state.city.map((val) =>
        val.names === name
          ? { names: name, ischecked: val.ischecked ? false : true }
          : val
      );
      return {
        datas: state.datas,
        city: citychecked,
      };
    case actiontypes.STATE_DATA:
      return state;
      break;

    case actiontypes.DISPLAYCITIES:
      const showcities = state.city.filter((val) => val.ischecked === true);
      const showstatearr = state.datas.filter((val) => val.ischecked === true);
      return {
        ...state,
        showcities: showcities,
        showstatearr: showstatearr,
      };
    default:
      return state;
  }
};
